@media (width >= 768px) {
  h1 {
    font-size: 3.6rem;
  }

  h2 {
    font-size: 1.8rem;
  }

  p, strong, a, span, button, input, label, select, option {
    font-size: 1.4rem;
  }

  #book-form div:last-child {
    justify-content: center;
    gap: 30px;
  }

  #book-form button {
    flex: unset;
    width: auto;
    min-width: 120px;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  #book-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    display: grid;
  }
}

@media (width >= 1024px) {
  #book-list {
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    display: grid;
  }

  .logo-img {
    padding-left: 10px;
  }

  header h1 {
    padding-right: 10px;
  }

  #burger-menu {
    display: none;
  }

  #side-menu {
    background: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    padding: 10px 0;
    display: flex;
    position: static;
  }

  #side-menu ul {
    gap: 20px;
    padding: 0;
    display: flex;
  }

  #side-menu ul li {
    padding: 0;
  }

  #side-menu ul li a {
    color: #1b3d2f;
    font-weight: bold;
  }

  #menu-overlay {
    display: none !important;
  }

  #close-menu {
    display: none;
  }

  .menu-icon {
    filter: brightness(100) invert();
  }
}
